  -moz-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%); */
  
   /* If you want to fully change the check appearance, use the following: */
   content: " ";
   width: 100%;
   height: 100%;
   background: blue;
   top: 0;
   left: 0;
  
}

.target_num{
  position: relative;
  bottom: 20px;
}

.card-box{
  padding: 20px;
  border: 1px solid 1px solid rgba(54, 64, 74, 0.08);
  border-radius: 5px;
  background-color: #36404a;
  margin-bottom: 20px;
}

.header-title{
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 700;
}

.header-title-big{
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 700;
}

#login{
  height: 500px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}


